import React from 'react'

function getRandomInt(max) {
  return Math.floor(Math.random() * Math.floor(max)) + 1;
}

const Item = ({ project }) => (
  <div
    className="item-outer"
    style={{
      flexGrow: getRandomInt(1), // Was 8
      flexShrink: getRandomInt(1), // Was 8
    }}
  >
    <div
      className="item"
      style={{
        left: getRandomInt(1), // Was 6
        top: getRandomInt(1), // Was 6
        flexGrow: getRandomInt(1), // Was 8
        flexShrink: getRandomInt(1), // Was 8
      }}
    >
      <a
        className="item-client"
        href={project.url}
        style={{
          transform: `scaleY(${getRandomInt(1)})`
        }}
      >
        {project.client}
      </a>
      <p
        className="item-title"
        style={{
          transform: `scaleY(${getRandomInt(1)})`
        }}
      >{project.title}</p>
      <p className="item-tagline">{project.tagline}</p>
    </div>
  </div>
)

Item.propTypes = {
  project: Object,
}

export default Item
